 <!--商品类型名称-->
<template>
  <div class="cms-list-layout">
    <cms-prompt collapse='true'>
      <li> 显示商品类型列表信息</li>
      <li>单个商品类型下对应不同的商品规格/属性及属性分组</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>商品类型名称</label>
        <el-input v-model="queryParams.name" @keyup.native.enter='query'></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <cms-sticky>
    <section class="cms-batch">
      <cms-button icon="add" type="success" size="small" 
       v-prem="'/productType/save'" @click="$routerLink('/productType/save','save')">新增</cms-button>
      <cms-button icon="error" type="error" size="small"  v-prem="'/productType/delete'" @click="deleteDatas()">批量删除</cms-button>
    </section>
    </cms-sticky>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' 
     @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="name" label="名称" min-width="80"></el-table-column>
      <el-table-column prop="groupName" label="属性分组" min-width="220">
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.groupName"> {{scope.row.groupName}}</div>
        </template>
      </el-table-column>
      <el-table-column label="是否允许自定义" min-width="100">
        <div slot-scope="scope">
          <span :class="[scope.row.custom?'green':'red']">
            {{scope.row.custom?'是':'否'}}
          </span>
        </div>
      </el-table-column>
      <el-table-column prop="groupSize" label="属性分组个数" ></el-table-column>
      <el-table-column prop="attrSize" label="属性个数" ></el-table-column>
      <el-table-column label="操作" width='320' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button icon='edit' size='small' type='info' @click.stop="$routerLink('/productType/standard/list','list',{id:scope.row.id,
           name:scope.row.name})" v-prem="'/productType/standard/list'">规格/属性列表</cms-button>
          <cms-button icon='edit' size='small' type='info' 
          v-prem="'/productType/update'" @click.stop="$routerLink('/productType/update','update',{id:scope.row.id})">编辑</cms-button>
          <cms-button icon='error' size='small' type='info' @click="deleteData(scope.row.id)" 
          v-prem="'/productType/delete'">删除
          </cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  productTypePage,
  productTypeDelete
} from '@/api/product/type.js'
var defaultData = {
  page: 1,
  size: 10,
  name: ''
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(productTypeDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(productTypeDelete, this.ids) // 这个才是真实的删除方法
    },
    query() {
      this.queryParams.page = 1
      this.getTableData(productTypePage, this.queryParams)
    }
  },
  created() {
    // 首次触发
    this.getTableData(productTypePage, this.queryParams)
  }
}
</script>
<style>
</style>



